import React, { useState, useEffect } from "react";
import { Layout } from "../../components/Layout";
import axios from "axios";

const UserApplications = () => {
  const [applications, setApplications] = useState([]);

  const getApplications = async () => {
    try {
      const res = await axios.get("/api/v1/user/get-user-applications", {
        headers: {
          Authorization: "Bearer " + localStorage.getItem("token"),
        },
      });

      if (res.data.success && Array.isArray(res.data.data)) {
        setApplications(res.data.data);
      } else {
        console.error("Unexpected response format:", res.data);
        setApplications([]);
      }
    } catch (error) {
      console.error("Error fetching applications:", error);
      setApplications([]);
    }
  };

  useEffect(() => {
    getApplications();
  }, []);

  console.log(applications);
  return (
    <Layout>
      <div className="min-h-screen bg-gray-900 text-white p-6">
        <h1 className="text-3xl font-bold mb-4 text-center">
          User Applications
        </h1>
        <div className="overflow-x-auto">
          <table className="min-w-full table-auto">
            <thead>
              <tr className="bg-gray-800 text-gray-400">
                <th className="px-4 py-2">Club Name</th>
                <th className="px-4 py-2">Student Roll No</th>
                <th className="px-4 py-2">Status</th>
                <th className="px-4 py-2">Message</th>
              </tr>
            </thead>
            <tbody>
              {applications.length > 0 ? (
                applications.map((application) => (
                  <tr
                    key={application._id}
                    className="bg-gray-700 hover:bg-gray-600 transition-colors duration-200"
                  >
                    <td className="border px-4 py-2">{application.clubName}</td>
                    <td className="border px-4 py-2">
                      {application.identifier}
                    </td>
                    <td className="border px-4 py-2">
                      <span
                        className={`font-semibold ${
                          application.status === "accepted"
                            ? "text-green-500"
                            : "text-red-500"
                        }`}
                      >
                        {application.status}
                      </span>
                    </td>
                    <td className="border px-4 py-2">{application.message}</td>
                  </tr>
                ))
              ) : (
                <tr>
                  <td colSpan="4" className="text-center py-4">
                    No applications found
                  </td>
                </tr>
              )}
            </tbody>
          </table>
        </div>
      </div>
    </Layout>
  );
};

export default UserApplications;
